
.inView {
  width: 800px;
}
.outOfView {
  width: 0px;
}
.loadedTitle {
  margin-bottom: 15px;
  width: 200px;
  font-weight: bold;
}
.stage {
  background-color: #fff;
  height: 600px;
  margin-bottom: 15px;
}
.stage-root {
  margin-top: 40px;
  height: 750px;
  margin-left: 30px;
  -webkit-transition: opacity 0.5s ease, width 0.5s ease;
  transition: opacity 0.5s ease, width 0.5s ease;
  overflow-x: hidden;
  overflow-y: hidden;
}
.btn-download {
  background-color: #ff0035;
  color: #fff;
  height: 40px;
  width: 150px;
  float: left;
  padding: 5px 15px;
  line-height: 40px;
  cursor: pointer;
}
.statBox {
  width: 800px;
  overflow-x: hidden;
  overflow-y: hidden;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.statBox .stat {
  float: left;
  background-color: #fff;
}
.stat {
  margin-right: 15px;
  height: 40px;
  line-height: 40px;
  padding: 5px 15px;
  font-size: 20px;
}
.list-item {
  display: inline-block;
}
.list-enter-active,
.list-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}


